9. Footer
*********************************/

.footer {
  width: 100%;
  background: #0c081d;
}

.footer_content {
  padding-top: 108px;
  padding-bottom: 115px;
}

.footer_about {
  width: 100%;
}

.footer .logo_sub {
  color: #bbbbbb;
}

.footer_about_text {
  margin-top: 49px;
}

.footer_about_text p {
  color: #bbbbbb;
}

.footer_links {
  padding-top: 32px;
}

.footer_links ul li:not(:last-child) {
  margin-bottom: 14px;
}

.footer_links ul li a {
  font-size: 16px;
  font-weight: 600;
  color: #bbbbbb;
}

.footer_extra {
  width: 100%;
  background: #050210;
}

.footer_extra_content {
  width: 100%;
  height: 134px;
}

.footer_social_title {
  display: inline-block;
  font-size: 16px;
  font-weight: 600;
  color: #bbbbbb;
  vertical-align: middle;
}

.footer_social_list {
  display: inline-block;
  margin-left: 42px;
  vertical-align: middle;
}

.footer_social_list li {
  display: inline-block;
}

.footer_social_list li:not(:last-child) {
  margin-right: 30px;
}

.footer_social_list li a i {
  color: #a09fa1;
  -webkit-transition: all 200ms ease;
  -moz-transition: all 200ms ease;
  -ms-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
}

.footer_social_list li a i:hover {
  color: #329fec;
}

.footer_extra_links ul li {
  display: inline-block;
  position: relative;
}

.footer_extra_links ul li a {
  font-size: 12px;
  font-weight: 600;
  color: #bbbbbb;
  -webkit-transition: all 200ms ease;
  -moz-transition: all 200ms ease;
  -ms-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
}

.footer_extra_links ul li a:hover {
  color: #329fec;
}

.footer_extra_links ul li:not(:last-child)::after {
  display: inline-block;
  content: "|";
  margin-left: 6px;
  margin-right: 3px;
  font-size: 12px;
  font-weight: 600;
  color: #bbbbbb;
}

