}
.header_social ul li a i {
  color: #4c4c4c;
  padding: 5px;
  transition: all 200ms ease;
}
.header_social ul li a i:hover {
  color: #329fec;
}
.hamburger {
  display: none;
  cursor: pointer;
}
.hamburger i {
  font-size: 18px;
  color: #4c4c4c;
  transition: all 200ms ease;
}
