.main_nav ul li a {
  display: block;
  position: relative;
  height: 100%;
  line-height: 82px;
  font-size: 16px;
  font-weight: 500;
  color: #4c4c4c;
  padding-left: 11px;
  padding-right: 8px;
  z-index: 1;
  transition: all 200ms ease;
}
.main_nav ul li a:hover,
.main_nav ul li.active a {
  color: #ffffff;
}
.search_container {
