/*********************************
5.1 Elements - Buttons
*********************************/

.buttons_container {
  margin-top: 114px;
}
.element_button {
  display: inline-block;
  margin-right: 16px;
  margin-bottom: 8px;
  vertical-align: middle;
}
.button_3 {
  border: solid 2px transparent;
  border-image: linear-gradient(to right, #4867c0, #329fec);
  border-image-slice: 1;
}
.button_3::before {
  background: transparent;
  opacity: 0;
  transition: all 200ms ease;
}
.button_3 a {
  color: #4c4c4c;
}
.button_3:hover::before {
  opacity: 1;
  background-color: #4867c0;
  background-image: linear-gradient(to right, #4867c0, #329fec);
  background: #4867c0;
  background: linear-gradient(to right, #4867c0, #329fec);
}
.button_3:hover a {
  color: #ffffff;
}

/*********************************
5.2 Elements - Accordions
*********************************/

.elements_accordions_tabs {
  margin-top: 115px;
}
.elements_accordions {
  width: 100%;
}
.accordions_tabs {
  width: 100%;
  background: #ffffff;
  margin-top: 100px;
}
.accordions {
  margin-top: 115px;
}
.accordion_container:not(:last-child) {
  margin-bottom: 14px;
}
.accordion {
  height: 57px;
  width: 100%;
  background: #f0f2f6;
  padding-left: 42px;
  cursor: pointer;
  color: #888888;
  font-size: 16px;
  font-weight: 500;
  transition: all 200ms ease;
}
.accordion div {
  max-width: 90%;
  overflow: hidden;
  white-space: nowrap;
}
.accordion::after {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  right: 12px;
  content: "+";
  font-size: 16px;
  color: #888888;
  font-weight: 600;
  transition: all 200ms ease;
}
.accordion.active {
